  @import "../common/style/variable";

  footer {
    width: 100%;
    background-color: $footerGrey;
    padding-top: 20px;
    font-size: 13px;
    padding-bottom: 30px;
    div.warp_up{
      max-width: $max-width;
      min-width: $min-width;
      margin: 0 auto;
      display: flex;
      div.item {
        flex: 1;
        border-right: 1px solid $fontGrey;
        div.title {
          padding: 5px 25px 10px;
          color: $fontDark;
        }
        li {
