        }
      }
    }
    div.warp_down {
      padding-top: 30px;
      font-size: 12px;
      display: flex;
      width: 1160px;
      margin: 0 auto;
      div.pic {
        background: url(data:image/png;base64,) no-repeat;
        width: 100px;
        height: 80px;
      }
      div.picRight {
        flex: auto;
        padding-left: 30px;
        color: $fontDark;
        p {
          padding: 3px 0;
        }
      }
    }
  }
</style>



